"use client";

import { useState } from 'react';
import { Calendar } from "@/components/ui/calendar";
import { Badge } from "@/components/ui/badge";

export default function WasteManagementPage() {
  const [date, setDate] = useState<Date | undefined>(new Date());

  // Calendrier de collecte des déchets
  const wasteSchedule = {
    0: [], // Dimanche
    1: ["Déchets ménagers", "Recyclables"], // Lundi
    2: ["Déchets verts"], // Mardi
    3: ["Déchets ménagers"], // Mercredi
    4: ["Encombrants"], // Jeudi
    5: ["Déchets ménagers", "Recyclables"], // Vendredi
    6: [], // Samedi
  };

  // Couleurs pour les différents types de déchets
  const wasteColors = {
    "Déchets ménagers": "bg-gray-500",
    "Recyclables": "bg-green-500",
    "Déchets verts": "bg-emerald-500",
    "Encombrants": "bg-orange-500"
  };

  // Fonction pour obtenir les collectes du jour
  const getDayCollections = (date: Date) => {
    return wasteSchedule[date.getDay() as keyof typeof wasteSchedule];
  };

  return (
    <div className="max-w-4xl mx-auto space-y-8 pb-12">
      <h2 className="text-3xl font-semibold mb-6">PPON H2030</h2>

      {/* Description */}
      <section className="bg-white p-6 rounded-lg shadow-md border border-gray-200">
        <h3 className="text-xl font-semibold mb-4">Gestion des déchets</h3>
        <p className="mb-4 text-gray-700">
          Le service de gestion des déchets assure la collecte et le traitement des différents types de déchets pour maintenir la propreté de notre ville.
        </p>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <h4 className="font-medium mb-2">Types de déchets collectés :</h4>
            <ul className="space-y-2">
              {Object.entries(wasteColors).map(([type, color]) => (
                <li key={type} className="flex items-center space-x-2">
                  <Badge className={color}>{type}</Badge>
                </li>
              ))}
            </ul>
          </div>
          <div>
            <h4 className="font-medium mb-2">Consignes de tri :</h4>
            <ul className="list-disc list-inside text-gray-700">
              <li>Utilisez les sacs appropriés</li>
              <li>Sortez vos déchets la veille au soir</li>
              <li>Respectez les jours de collecte</li>
              <li>Nettoyez les recyclables</li>
            </ul>
          </div>
        </div>
      </section>

      {/* Calendrier */}
      <section className="bg-white p-6 rounded-lg shadow-md border border-gray-200">
        <h3 className="text-xl font-semibold mb-4">Calendrier des collectes</h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          <div>
            <Calendar
              mode="single"
              selected={date}
              onSelect={setDate}
              className="rounded-md border"
            />
          </div>
          <div>
            <h4 className="font-medium mb-4">
              Collectes du {date?.toLocaleDateString('fr-FR', { weekday: 'long', day: 'numeric', month: 'long' })}
            </h4>
            <div className="space-y-4">
              {date && getDayCollections(date).length > 0 ? (
                getDayCollections(date).map((type, index) => (
                  <div key={index} className="flex items-center space-x-2">
                    <Badge className={wasteColors[type as keyof typeof wasteColors]}>
                      {type}
                    </Badge>
                    <span className="text-gray-600">
                      {type === "Déchets ménagers" && "- À sortir avant 20h"}
                      {type === "Recyclables" && "- Bien rincer les contenants"}
                      {type === "Déchets verts" && "- En sacs biodégradables uniquement"}
                      {type === "Encombrants" && "- Sur rendez-vous"}
                    </span>
                  </div>
                ))
              ) : (
                <p className="text-gray-500">Pas de collecte prévue ce jour</p>
              )}
            </div>
          </div>
        </div>
      </section>

      {/* Informations supplémentaires */}
      <section className="bg-white p-6 rounded-lg shadow-md border border-gray-200">
        <h3 className="text-xl font-semibold mb-4">Informations pratiques</h3>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <h4 className="font-medium mb-2">Points de collecte spéciaux</h4>
            <ul className="list-disc list-inside text-gray-700 space-y-2">
              <li>Déchetterie centrale - Ouvert 7j/7</li>
              <li>Points d'apport volontaire - Verre</li>
              <li>Centres de recyclage - Électronique</li>
            </ul>
          </div>
          <div>
            <h4 className="font-medium mb-2">Contact</h4>
            <p className="text-gray-700">
              Pour toute question ou demande spéciale :<br />
              Tél : 123-456-789<br />
              Email : dechets@ppon2030.cd
            </p>
          </div>
        </div>
      </section>
    </div>
  );
}
